
import { reactive, computed, onBeforeMount } from 'vue'
import {useStore} from 'vuex'
export default {
  name: 'invite-dialog',
  props: {
    open: {
      type: Boolean,
      default: false
    },
    info: {
      type: Object,
      required: true,
    },
  },
  setup(props, { emit }) {
    const store = useStore();
    const state = reactive({
      dialogVisible: computed(() => props.open),
      inputEmail: '',
      teamId:null,
    })

    const handleClose = function () {
      state.inputEmail = ''
      emit('closeInviteDialog')
    }

    const addTeamMember = function(){
      store.dispatch('root/addTeamMember', { 'teamId': state.teamId, 'userId': state.inputEmail})
      .then(function (result) {
              swal({
                  title: "팀 초대 등록",
                  text: "팀 초대 등록이 완료되었습니다.",
                  icon: "success",
                  button: "확인",
                });

              // router.go(router.currentRoute)




      })
      .catch(function (error) {
        console.log(error.response.data)
        swal({
              title: "팀 초대 등록",
              text: error.response.data.message,
              icon: "error",
              button: "확인",
            });

        handleClose()
      })
    }

    onBeforeMount(() => {
      //state.team.teamId = props.info.teamId;
      console.log(props.info);
      state.teamId = props.info;
      //이번 달 달력 가져오기
      //store.commit()
    })
    return { state, handleClose, addTeamMember }
  }
}
